@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
* {
  scroll-behavior: smooth;
}

.header {
  width: 100%;
  cursor: pointer;
  position: fixed;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  background-color: #101820ff;
  font-family: "Montserrat", sans-serif;
  font-size: 30px;
  padding-bottom: 15px;
  box-shadow: 0px 1px 5px black;
  color: white;
  z-index: 100;
}

@media (max-width: 1000px) {
  .header {
    padding-top: 15px;
    font-size: 30px;
  }
}

.app {
  background-color: #101820ff;
  color: white;
  padding-top: 70px;
  padding-bottom: 70px;
}

@media (max-width: 700px) {
  .app {
    padding-top: 80px;
  }
}

.media {
  display: flex;
  flex-direction: column;
  /* width: 200px; */
  min-height: 540px;
  padding: 5px;
  margin: 5px 0;
  border: 1px solid black;
  color: #fee715ff;
  background-color: #101820ff;
  border-radius: 10px;
  position: relative;
  font-family: "Montserrat", sans-serif;
}

.media:hover {
  background-color: rgb(72, 84, 104);
  color: black;
}

/* @media (max-width: 550px) {
  .media {
    width: 46%;
  }
} */

.poster {
  border-radius: 10px;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 17px;
  padding: 8px 0;
}

.subTitle {
  display: flex;
  justify-content: space-between;
  padding-bottom: 3px;
  padding: 0 2px;
  padding-bottom: 3px;
}

.trending {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.pagesTitle {
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  font-size: 2vw;
  padding: 4px;
  border-radius: 50px;
  color: white;
}

@media (max-width: 1000px) {
  .pagesTitle {
    font-size: 6.4vw;
  }
}

/* Frame */

html,
body {
  min-height: 100%;
}

#root {
  min-height: 100vh;
}
.app {
  min-height: 100vh !important;
}
.cont {
  min-height: 100%;
}

.frame {
  min-height: 75vh;
}


.ContentModal__landscape {
  object-fit: contain;
  border-radius: 10px;
}

.ContentModal__portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
}

.ContentModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.ContentModal::-webkit-scrollbar {
  display: none;
}

.ContentModal__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  justify-content: space-evenly;
  font-weight: 300;
}

.ContentModal__title {
  height: 20%;
  font-size: 5vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentModal__description {
  display: flex;
  height: 40%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  scrollbar-width: thin; /* Firefox */
  box-shadow: inset 0 0 5px #000000;
  text-align: justify;
}

.ContentModal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .ContentModal__landscape {
    display: none;
  }
  .ContentModal__portrait {
    display: flex;
    width: 38%;
  }
  .ContentModal {
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }
  .ContentModal__about {
    width: 58%;
    padding: 0;
    height: 100%;
  }
  .ContentModal__title {
    font-size: 3.5vw;
  }
  .ContentModal__description {
    font-size: 22px;
  }
}

.inputs {
  color: rgb(255, 240, 254);
  background-color: rgba(0, 0, 0, 0.1);
}

.carouselItem {
  display: flex;
  flex-direction: column;
  object-fit: contain;
  padding: 10px;
}
.carouselItem__img {
  border-radius: 10px;
  margin-bottom: 5px;
  box-shadow: 0px 0px 5px black;
}
